@import "./../../styles/design";

.dots {
  position: absolute;
  bottom: 25px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.dot {
  padding: 10px;
  margin-right: 5px;
  cursor: pointer;
  border-radius: 50%;
  background: $c-white;
  &.active {
    background: $c-salmon;
  }
}
